.air__stations {
    height: 100vh;
    z-index: 50;
    /* filter: grayscale(0.6); */
}

.air__stations-marker-cluster {
    background: var(--color-secondary-weak);
    border-radius: 50%;
    color: #ededed;
    height: 40px;
    line-height: 40px;
    text-align: center;
    width: 40px;
    box-sizing: border-box;
    font-family: "Open Sans", sans-serif;
}

.air__stations-upperaustria-marker-wrapper {
    border: 0;
    background: transparent;
}

.air__stations-upperaustria-marker {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.air__stations-upperaustria-marker svg,
.air__stations-luftdaten-marker svg {
    -webkit-transition: fill var(--animation-duration) var(--animation-timing);
    transition: fill var(--animation-duration) var(--animation-timing);
}

/* ------- */
/* LEAFLET */
/* ------- */

.leaflet-left .leaflet-control {
    margin-left: 12px;
    margin-top: 12px;
}

.leaflet-touch .leaflet-control-layers, .leaflet-touch .leaflet-bar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 0;
    background: transparent;
    /* box-shadow: 2px 4px 12px var(--shadow); */
}

.leaflet-touch .leaflet-bar a {
    width: 32px;
    height: 32px;
    line-height: 32px;
    background-color: var(--color-text);
    color: var(--color-primary-active);
}

.leaflet-touch .leaflet-bar a:first-child,
.leaflet-touch .leaflet-bar a:last-child {
    border-radius: 0;
}

.leaflet-control-attribution {
    position: fixed;
    top: -16px;
    right: 0;
    background: rgba(255,255,255,0.5);
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    text-align: right;
}
.air__comp {
    display: flex;
    justify-content: center;
    color: var(--color-text);
    line-height: 1.35;
}

.air__comp--dust .air__comp-title,
.air__comp--dust .air__comp-value {
    width: 90px;
}

.air__comp--dust .air__comp-title {
    text-align: right;
}

.air__comp--dust .air__comp-value {
    text-align: left;
    margin-left: 4px;
}

.air__comp--dust-PM10kont {
    font-size: 16px !important;
}

.air__comp-wind-direction {
    margin-top: 10px;
    font-size: 12px;
    color: var(--color-text);
    line-height: 1.2;
    -webkit-transition: -webkit-transform var(--animation-duration) var(--animation-timing);
    transition: -webkit-transform var(--animation-duration) var(--animation-timing);
    transition: transform var(--animation-duration) var(--animation-timing);
    transition: transform var(--animation-duration) var(--animation-timing), -webkit-transform var(--animation-duration) var(--animation-timing);
    -webkit-transform-origin: center center;
            transform-origin: center center;
    will-change: transform;
}

.air__comp-wind-direction-svg {
    width: 18px;
    height: 18px;
}

.air__comp-wind-direction-svg path {
    fill: var(--color-text);
}

.air__comp-wind-force {
    font-size: 12px;
    color: var(--color-text);
    line-height: 1.2;
}

.air__comp--temp {
    order: 1;
}
.air__compass {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    color: var(--color-text);
}

.air__compass:after {
    content: "";
    position: absolute;
    border-radius: 50%;
    border: 1px dashed var(--color-text-weak);
    top: 22px;
    bottom: 22px;
    right: 22px;
    left: 22px;
}

.air__compass-wind {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    will-change: transform;
    -webkit-transition: -webkit-transform ease-out 150ms;
    transition: -webkit-transform ease-out 150ms;
    transition: transform ease-out 150ms;
    transition: transform ease-out 150ms, -webkit-transform ease-out 150ms;
}

.air__compass-wind-svg {
    -webkit-transform: rotate(90deg) matrix(-1, 0, 0, 1, 0, 0) translatex(16px);
            transform: rotate(90deg) matrix(-1, 0, 0, 1, 0, 0) translatex(16px);
    width: 8px;
    height: 8px;
    position: absolute;
    top: 42px;
}

.air__compass-wind-svg path {
    fill: rgba(255, 255, 255, 0.75);
}

.air__compass-grid {
    will-change: transform;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    will-change: transform;
    -webkit-transition: -webkit-transform ease-out 150ms;
    transition: -webkit-transform ease-out 150ms;
    transition: transform ease-out 150ms;
    transition: transform ease-out 150ms, -webkit-transform ease-out 150ms;
    color: var(--color-text);
}

.air__compass-grid-n {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1;
    width: 20px;
    height: 20px;
    position: absolute;
    top: 0;
    right: calc(50% - 10px);
    will-change: transform;
    -webkit-transition: -webkit-transform ease-out 150ms;
    transition: -webkit-transform ease-out 150ms;
    transition: transform ease-out 150ms;
    transition: transform ease-out 150ms, -webkit-transform ease-out 150ms;
}

.air__compass-grid-o {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1;
    width: 20px;
    height: 20px;
    position: absolute;
    top: calc(50% - 10px);
    right: 0;
    will-change: transform;
    -webkit-transition: -webkit-transform ease-out 150ms;
    transition: -webkit-transform ease-out 150ms;
    transition: transform ease-out 150ms;
    transition: transform ease-out 150ms, -webkit-transform ease-out 150ms;
}

.air__compass-grid-s {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1;
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 0px;
    right: calc(50% - 10px);
    will-change: transform;
    -webkit-transition: -webkit-transform ease-out 150ms;
    transition: -webkit-transform ease-out 150ms;
    transition: transform ease-out 150ms;
    transition: transform ease-out 150ms, -webkit-transform ease-out 150ms;
}

.air__compass-grid-w {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1;
    width: 20px;
    height: 20px;
    position: absolute;
    top: calc(50% - 10px);
    left: 0;
    will-change: transform;
    -webkit-transition: -webkit-transform ease-out 150ms;
    transition: -webkit-transform ease-out 150ms;
    transition: transform ease-out 150ms;
    transition: transform ease-out 150ms, -webkit-transform ease-out 150ms;
}

.air__compass-grid-n-line,
.air__compass-grid-o-line,
.air__compass-grid-s-line,
.air__compass-grid-w-line {
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 1px;
    height: 100%;
    background: var(--color-text-weak);
    will-change: transform;
    -webkit-transition: -webkit-transform ease-out 150ms;
    transition: -webkit-transform ease-out 150ms;
    transition: transform ease-out 150ms;
    transition: transform ease-out 150ms, -webkit-transform ease-out 150ms;
}
.air__button,
.air__button:focus,
.air__button:active {
    box-sizing: border-box;    
    padding: 4px 8px;
    min-height: 32px;
    min-width: 32px;
    border: 1px solid white;
    border-radius: 6px;
    background: var(--color-text);
    color: rgba(0, 121, 107, 1);
    box-shadow: 0;
    outline: 0;
}

.air__button--naked,
.air__button--naked:focus,
.air__button--naked:active {
    border: 0;
    border-radius: 0;
    box-shadow: 0;
}

.air__button--ghost,
.air__button--ghost:focus,
.air__button--ghost:active {
    background: transparent;
}
.air__station {
    --height: 288px;
    --width: 288px;
    --offset: 5vw;
    --shadow: rgba(0, 0, 0, 0.5);
    
    background-color: transparent;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: var(--width);
    height: var(--height);;
    margin-top: 44px;
    position: fixed;
    left: var(--offset);
    z-index: 100;
    will-change: opacity, transform, box-shadow, background-color;
    -webkit-transition: background-color var(--animation-duration) var(--animation-timing);
    transition: background-color var(--animation-duration) var(--animation-timing);
    /* box-shadow: 5px 10px 30px var(--shadow); */
}

@media (min-width: 330px) {
    .air__station {
        --width: 330px;
        --height: 330px;
        --offset: calc(50% - 165px);
    }
}

.air__station-background {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: -1;
    will-change: fill;
    -webkit-transition: fill var(--animation-duration) var(--animation-timing);
    transition: fill var(--animation-duration) var(--animation-timing);
    -webkit-transform: scale(calc(600 / 590));
            transform: scale(calc(600 / 590));
    /* clip-path: circle(180px); */
}

.a-station-appear {
    /* opacity: 0; */
    -webkit-transform: scale(0);
            transform: scale(0);
}

.a-station-appear.a-station-appear-active {
    /* opacity: 1; */
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: opacity var(--animation-duration-crunchy) var(--animation-timing), -webkit-transform var(--animation-duration-crunchy) var(--animation-timing);
    transition: opacity var(--animation-duration-crunchy) var(--animation-timing), -webkit-transform var(--animation-duration-crunchy) var(--animation-timing);
    transition: transform var(--animation-duration-crunchy) var(--animation-timing), opacity var(--animation-duration-crunchy) var(--animation-timing);
    transition: transform var(--animation-duration-crunchy) var(--animation-timing), opacity var(--animation-duration-crunchy) var(--animation-timing), -webkit-transform var(--animation-duration-crunchy) var(--animation-timing)
}

.a-station-leave {
    opacity: 1;
}

.a-station-leave.a-station-leave-active {
    -webkit-transition: opacity var(--animation-duration) var(--animation-timing);
    transition: opacity var(--animation-duration) var(--animation-timing);
}

.air__station-header {
    /* background: rgba(0, 0, 0, 0.75); */
    font-size: 20px;
    color: var(--color-text);
    padding: 4px 40px;
    line-height: 1.2;
    margin-bottom: 0;
    margin-top: 0;
    width: 80vw;
    max-width: 330px;
    box-sizing: border-box;
    line-height: 1.1;
    order: 1;
}

.air__station-date {
    font-size: 11px;
    font-style: italic;
    color: var(--color-text);
    margin-bottom: 10px;
    margin-top: -4px;
    order: 2;
}

.air__comp--dust-NO2 {
    order: 3;
}

.air__comp--dust-PM10 {
    order: 4;
}

.air__comp--dust-PM25 {
    order: 5;
}

.air__comp--temp {
    order: 8;
}

.air__comp-wind-direction {
    order: 6;
}

.air__comp-wind-force {
    order: 7;
}

.air__station-date + .air__comp-wind {
    margin-top: 0;
}

.air__action-container {
    display: flex;
    position: absolute;
    bottom: 24px;
}

.air__station-button-icon {
    width: 18px;
    height: 18px;
}

.air__station-button-icon path {
    -webkit-transition: fill var(--animation-duration) var(--animation-timing);
    transition: fill var(--animation-duration) var(--animation-timing);
    will-change: fill;
}

