.air__legend-scale {
    position: absolute;
    bottom: 0;
    left: 12px;
    height: 100%;
    width: 52px;
    padding-left: 4px;
    background: rgba(255,255,255,0.75);
}

.air__legend-scale-stop {
    position: absolute;
    font-size: 11px;
    text-align: left;
}

.air__legend-scale-stop:after {
    content: "";
    background: rgba(255,255,255,0.75);
    position: absolute;
    width: 12px;
    height: 1px;
    left: -16px;
    bottom: 0;
}

.air__legend-scale-stop:first-child:after,
.air__legend-scale-stop:last-child:after {
    display: none;
}

.air__legend-scale-stop--0 {
    bottom: 0;
    white-space: nowrap;
}

.air__legend-scale-stop--1 {
    bottom: 20%;
}

.air__legend-scale-stop--2 {
    bottom: 40%;
}

.air__legend-scale-stop--3 {
    bottom: 60%;
}

.air__legend-scale-stop--4 {
    bottom: 80%;
}

.air__legend-scale-stop--5 {
    bottom: calc(100% -12px);
}

.air__legend-scale--limit {
    color: rgba(255,0,0,1);
    font-weight: bold;
}

.air__legend-scale--limit-text {
    position: absolute;
    top: -14px;
}

.air__legend-scale--limit.air__legend-scale-stop:after {
    background: rgba(255,0,0,0.5);
    height: 2px;
    left: -18px;
    width: 16px;
}
.air__update-bar {
    position: fixed;
    bottom: 0;
    height: calc(var(--margin) / 2);
    width: 100%;
    background: rgba(0, 121, 107, 0.75);
    z-index: 100;
    will-change: transform; 
    -webkit-transform-origin: left; 
            transform-origin: left;
  }
/*
 * CUSTOM PROPERTIES
 */

:root {
  --color-primary: rgba(0, 121, 107, 0.75);
  --color-primary-active: rgba(0, 121, 107, 0.9);
  --color-secondary: rgba(37, 113, 199, 0.9);
  --color-secondary-weak: rgba(37, 113, 199, 0.5);
  --color-text: rgba(255, 255, 255, 0.9);
  --color-text-weak: rgba(255, 255, 255, 0.3);
  --margin: 0.5rem;
  --border-radius: calc(var(--margin) / 3);
  --shadow: rgba(0, 0, 0, 0.5);
  --animation-duration: 300ms;
  --animation-duration-crunchy: calc(var(--animation-duration) / 2);
  --animation-timing: ease-out;
}

.air {
  height: inherit;
  width: inherit;
  /* padding-top: 32px; */
  overflow: hidden;
}

.air__background {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0.2;
  z-index: -1;
}

.air__background svg {
  width: 50%;
  height: 50%;
  -webkit-transform: rotate(36deg);
          transform: rotate(36deg);
}

.air__background path {
  fill: rgba(120, 120, 120, 0.3)
}

.air__logo {
  font-size: 10px;
  color: rgba(0, 121, 107, 0.75);
  color: var(--color-primary);
  /* background: var(--color-text-weak); */
  /* padding: 4px 0; */
  position: absolute;
  right: 12px;
  bottom: 56px;
  width: 56px;
  justify-content: center;
  display: flex;
  align-items: center;
}

.air__logo-svg {
  width: 12px;
  height: 12px;
  margin-right: 1px;
  margin-top: -1px;
  background: rgba(0, 121, 107, 0.75);
  background: var(--color-primary);
  /* padding: 3px; */
  border-radius: 15%;
}

.air__logo-svg path {
  fill: rgba(255, 255, 255, 0.9);
  fill: var(--color-text);
}

.air__station-date + .air__comp-wind {
  margin-top: 0;
}

.air__footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 44px;
  -webkit-transition: -webkit-transform 300ms ease-out;
  transition: -webkit-transform 300ms ease-out;
  transition: transform 300ms ease-out;
  transition: transform 300ms ease-out, -webkit-transform 300ms ease-out;
  -webkit-transition: -webkit-transform var(--animation-duration) var(--animation-timing);
  transition: -webkit-transform var(--animation-duration) var(--animation-timing);
  transition: transform var(--animation-duration) var(--animation-timing);
  transition: transform var(--animation-duration) var(--animation-timing), -webkit-transform var(--animation-duration) var(--animation-timing);
  will-change: transform;
  z-index: 50;
}

/* ################################### */

.air__button-group {
  display: flex;
  justify-content: space-between;
  position: fixed;
  bottom: calc(0.5rem * 2);
  bottom: calc(var(--margin) * 2);
  right: calc(50% - 120px);
  z-index: 100;
  width: 240px;
  border-radius: var(--borderRadius);
  overflow: hidden;
  /* box-shadow: 2px 4px 12px var(--shadow); */
}

.air__button-group .air__button {
  width: calc(33.3% - 0.5px);
}

.air__updating,
.air__loading {
  position: fixed;
  width: 140px;
  top: calc(50% - 8px);
  left: calc(50% - 70px);
  text-align: center;
  color: rgba(0, 121, 107, 1);
  background-color: rgba(255, 255, 255, 0.9);
  background-color: var(--color-text);
  padding: 8px;
  border: 1px solid rgba(255, 255, 255, 0.9);
  border: 1px solid var(--color-text);
  border-radius: var(--borderRadius);
  /* box-shadow: 2px 4px 12px var(--shadow); */
  text-transform: uppercase;
  font-size: 14px;
  font-weight: bold;
  line-height: 1;
  display: inline-block;
  z-index: 100;
}

.air__spacer {
  flex-grow: 1;
  background: rgba(0, 121, 107, 0.75)
}

.air__animation-fade-appear,
.air__animation-fade-crunchy-appear {
  opacity: 0;
  -webkit-transition: opacity 300ms ease-out;
  transition: opacity 300ms ease-out;
  -webkit-transition: opacity var(--animation-duration) var(--animation-timing);
  transition: opacity var(--animation-duration) var(--animation-timing);
}

.air__animation-fade-crunchy-appear {
  -webkit-transition: opacity calc(300ms / 2) ease-out;
  transition: opacity calc(300ms / 2) ease-out;
  -webkit-transition: opacity var(--animation-duration-crunchy) var(--animation-timing);
  transition: opacity var(--animation-duration-crunchy) var(--animation-timing);
}

.air__animation-fade-appear.air__animation-fade-appear-active,
.air__animation-fade-crunchy-appear.air__animation-fade-crunchy-appear-active {
  opacity: 1;
}

.air__animation-fade-leave,
.air__animation-fade-crunchy-leave {
  opacity: 1;
}

.air__animation-fade-leave.air__animation-fade-leave-active,
.air__animation-fade-crunchy-leave.air__animation-fade-crunchy-leave-active {
  opacity: 0;
  -webkit-transition: opacity 300ms ease-out;
  transition: opacity 300ms ease-out;
  -webkit-transition: opacity var(--animation-duration) var(--animation-timing);
  transition: opacity var(--animation-duration) var(--animation-timing);
}

.air__animation-fade-crunchy-leave.air__animation-fade-crunchy-leave-active {
  -webkit-transition: opacity calc(300ms / 2) ease-out;
  transition: opacity calc(300ms / 2) ease-out;
  -webkit-transition: opacity var(--animation-duration-crunchy) var(--animation-timing);
  transition: opacity var(--animation-duration-crunchy) var(--animation-timing);
}
