.air__station {
    --height: 288px;
    --width: 288px;
    --offset: 5vw;
    --shadow: rgba(0, 0, 0, 0.5);
    
    background-color: transparent;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    width: var(--width);
    height: var(--height);;
    margin-top: 44px;
    position: fixed;
    left: var(--offset);
    z-index: 100;
    will-change: opacity, transform, box-shadow, background-color;
    transition: background-color var(--animation-duration) var(--animation-timing);
    /* box-shadow: 5px 10px 30px var(--shadow); */
}

@media (min-width: 330px) {
    .air__station {
        --width: 330px;
        --height: 330px;
        --offset: calc(50% - 165px);
    }
}

.air__station-background {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: -1;
    will-change: fill;
    transition: fill var(--animation-duration) var(--animation-timing);
    transform: scale(calc(600 / 590));
    /* clip-path: circle(180px); */
}

.a-station-appear {
    /* opacity: 0; */
    transform: scale(0);
}

.a-station-appear.a-station-appear-active {
    /* opacity: 1; */
    transform: scale(1);
    transition: transform var(--animation-duration-crunchy) var(--animation-timing), opacity var(--animation-duration-crunchy) var(--animation-timing)
}

.a-station-leave {
    opacity: 1;
}

.a-station-leave.a-station-leave-active {
    transition: opacity var(--animation-duration) var(--animation-timing);
}

.air__station-header {
    /* background: rgba(0, 0, 0, 0.75); */
    font-size: 20px;
    color: var(--color-text);
    padding: 4px 40px;
    line-height: 1.2;
    margin-bottom: 0;
    margin-top: 0;
    width: 80vw;
    max-width: 330px;
    box-sizing: border-box;
    line-height: 1.1;
    order: 1;
}

.air__station-date {
    font-size: 11px;
    font-style: italic;
    color: var(--color-text);
    margin-bottom: 10px;
    margin-top: -4px;
    order: 2;
}

.air__comp--dust-NO2 {
    order: 3;
}

.air__comp--dust-PM10 {
    order: 4;
}

.air__comp--dust-PM25 {
    order: 5;
}

.air__comp--temp {
    order: 8;
}

.air__comp-wind-direction {
    order: 6;
}

.air__comp-wind-force {
    order: 7;
}

.air__station-date + .air__comp-wind {
    margin-top: 0;
}

.air__action-container {
    display: flex;
    position: absolute;
    bottom: 24px;
}

.air__station-button-icon {
    width: 18px;
    height: 18px;
}

.air__station-button-icon path {
    transition: fill var(--animation-duration) var(--animation-timing);
    will-change: fill;
}

/*------------------------------------*\
  #SPINNER
\*------------------------------------*/

.air__station .spinner {
    position: absolute;
    height: 100%;
}

.air__station .spinner__backdrop {
    display: none;
    border-radius: 50%;
    background: transparent;
}